<template>
  <div id="app">
    <h2> Increment and Decrement a Number </h2>
    <div class="counter"> {{ counter }} </div>
    <button @click="increment" class="increment"> Increment </button>
    <button @click="decrement" class="decrement"> Decrement </button>
    <button @click="reset" class="reset"> Reset </button>
  </div>
</template>

<script>
export default {
  methods: {
    increment () {
      this.$store.commit('increment')
    },
    decrement () {
      this.$store.commit('decrement')
    },
    reset () {
      this.$store.commit('reset')
    }
  },
  computed: {
    counter () {
      return this.$store.getters.getCounter
    }
  }
}
</script>

<style scoped>
#app {
  width: 80%;
  margin: 100px auto;
  background: #f1f1f1;
  padding: 15px;
}
.counter {
  width: 30%;
  padding: 50px 5px;
  margin: 20px auto;
  background: #0fb9b1;
  color:#fff;
  font-size: 100px;
}
button {
  border: 0;
  padding: 13px 45px;
  font-weight: bold;
  margin-bottom: 20px;
}
.increment {
  background: #2c2c54;
  color: #fff;
}
.increment:hover {
  transition: .7s;
  opacity: 0.8;
  cursor: pointer;
}
.decrement {
  background: #c44569;
  color: #fff;
}
.decrement:hover {
  transition: .7s;
  opacity: 0.9;
  cursor: pointer;
}
.reset {
  background: #2E8B57;
  color: #fff;
}
.reset:hover {
  transition: .7s;
  opacity: 0.8;
  cursor: pointer;
}
</style>



